.app-wrapper {
    background: rgb(237, 238, 240);
    margin: 0 auto;
    display: grid;
    width: 100%;
    min-height: 100vh;
    grid-template-areas:
  "h h"
  "n c";

    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    /* grid-gap: 10px; */
}

.app-wrapper-content {
    grid-area: c;
    background: rgb(237, 238, 240);
}