.login{
    padding: 10px;
    background: #fff;
    border-radius: 4px;
    box-shadow: 0 1px 0 0 #d3d9de, 0 0 0 1px #e7e8ec;
    margin: 15px 15px 0;
}
.formContainer{
    display: flex;
    justify-content: center;
}
.formContainer form div:nth-child(3) div:nth-child(1) {
    position: absolute;
    margin-left: 45px;
    margin-top: -2px;
}
.formContainer form div div div input {
    padding: 10px 0;
    margin: 10px 0;
    width: 185px;
    border: solid 2px #ccc;
}
.loginBtn {
    width: 190px;
    padding: 7px 0;
    margin-top: 10px;
    background: #5181b8;
    border: 2px solid #ccc;
    color: #fff;
    cursor: pointer;
}