.selected{
    font-weight: bold;
}
.cursor{
    cursor:pointer;
}
.paginator {
    margin: 10px;
}
.pageNumber {
    padding: 2px;
    border: 1px solid grey;
    cursor: pointer;
}
.pageNumber:hover {
    color: #2a5885;
    border: 1px solid #2a5885;
}

.pageNumber.selectedPage {
    font-weight: bold;
    border-color: black;
}
.prevNextBtn{
    background: #5181b8;
    color: #fff;
    border: none;
    padding: 5px;
    width: 90px;
    cursor: pointer;
    margin: 10px 15px;
    height: 22px;
}