.user:nth-child(4),
.user:nth-child(5),
.user:nth-child(6) {
    display: block;
}
.userName{
    font-weight: bold;
}
.usersContainer {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    background: #fff;
    border-radius: 4px;
    box-shadow: 0 1px 0 0 #d3d9de, 0 0 0 1px #e7e8ec;
    margin: 15px 15px 0;
}
.userPaginator{
    display: flex;
    justify-content: center;
}
.photo {
    width:75px;
    height: 75px;
}
.userPhoto{
    width:75px;
}
.btnFollow button {
    background: #5181b8;
    color: #fff;
    border: none;
    padding: 5px;
    width: 90px;
    margin: 10px 0;
    cursor: pointer;
}


